// @flow
import * as React from 'react';
import {MenuButtonProps} from "../../../../component/button/MenuButton";
import {MultiStatusButton} from "../../../../component/button/MultiStatusButton";
import FormatColorTextIcon from '@mui/icons-material/FormatColorText';
import CategoryIcon from '@mui/icons-material/Category';
import ViewInArIcon from '@mui/icons-material/ViewInAr';
import VisibilityIcon from '@mui/icons-material/Visibility';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import {DriverFileProfile} from "../../../../proto/application/driver/DriverMessage";

export type SorterStatus = "name" | "size" | "time" | "type" | "visibility";

const statusToSorterButtonProps = {
  name: {
    title: "Name",
    icon: <FormatColorTextIcon/>
  },
  type: {
    title: "Type",
    icon: <CategoryIcon/>
  },
  time: {
    title: "Last Modified",
    icon: <AccessTimeIcon/>
  },
  size: {
    title: "Size",
    icon: <ViewInArIcon/>
  },
  visibility: {
    title: "Visibility",
    icon: <VisibilityIcon/>
  }
}

type SorterButtonProps = {
  currentSorter: SorterStatus,
  onSorterChange: (newSorter: SorterStatus) => void,
} & Omit<MenuButtonProps, "menuItems">;

export const SorterButton = ({
                               currentSorter,
                               onSorterChange,
                               ...rest
                             }: SorterButtonProps) => {
  return <MultiStatusButton
      currentStatus={currentSorter}
      statusToContentButtonProps={statusToSorterButtonProps}
      onStatusChange={(status) => {
        onSorterChange(status as SorterStatus);
      }}
      icon={statusToSorterButtonProps[currentSorter].icon}
      {...rest}
  ></MultiStatusButton>
};

export const fileComparator = (a: DriverFileProfile, b: DriverFileProfile, sorter: SorterStatus, reverse: boolean) => {
  let result = 0;

  // 文件夹排在前面
  if (a.isFolder && !b.isFolder) {
    result = 1;
    return reverse ? -result : result;
  } else if (!a.isFolder && b.isFolder) {
    result = -1;
    return reverse ? -result : result
  }

  switch (sorter) {
    case "size":
      result = (a.fileMetaData?.sizeInBytes || 0) - (b.fileMetaData?.sizeInBytes || 0);
      break;
    case "time":
      result = a.lastModifiedDate - b.lastModifiedDate;
      break;
    case "type":
      result = (a.fileMetaData?.contentType || "").localeCompare(b.fileMetaData?.contentType || "");
      break;
    case "visibility":
      result = a.isPublic ? 1 : -1 - (b.isPublic ? 1 : -1);
      break;
    case "name":
    default:
      result = a.name.localeCompare(b.name);
      break;
  }

  if (result === 0) {
    // id must be unique
    result = a.id.localeCompare(b.id);
  }

  return reverse ? -result : result;

}