import * as React from 'react';
import {useEffect, useRef} from 'react';
import ListComponent from "../../component/container/ListComponent";
import {DriverFileProfile} from "../../proto/application/driver/DriverMessage";
import {FileItemRow} from "./component/FileItemRow";
import {FileItemBlock} from "./component/FileItemBlock";
import DriveFileMoveIcon from '@mui/icons-material/DriveFileMove';
import {Breadcrumbs, Checkbox, Divider, Link, Stack, StackProps, Switch} from "@mui/material";
import {Grid} from "@mui/system";
import {useSearchParams} from "react-router-dom";
import DeleteIcon from '@mui/icons-material/Delete';
import AlertContext from "../../context/AlertContext";
import {getLink, getReadableFileSize, getTotalSize} from "./util/Utils";
import {OpenFolderButton} from "./component/actions/OpenFolderButton";
import {ShareButton} from "./component/actions/ShareButton";
import {DeleteButton} from "./component/actions/DeleteButton";
import {FileRenameButton} from "./component/actions/FileRenameButton";
import SortIcon from '@mui/icons-material/Sort';
import {LayoutStatus, LayoutSwitchButton} from "./component/actions/LayoutSwitchButton";
import OneOf from "../../component/container/OneOf";
import {CustomerProfile} from "../../proto/framework/customer/CustomerInfo";
import {deleteFile, getFile, getFileId, moveFile} from "./service/DriverService";
import MenuIcon from '@mui/icons-material/Menu';
import CreateNewFolderIcon from '@mui/icons-material/CreateNewFolder';
import {fileComparator, SorterButton, SorterStatus} from "./component/actions/SorterButton";
import ContentButton from "../../component/button/ContentButton";
import {useTranslation} from "react-i18next";
import BreakpointContainer from "../../component/container/BreakpointContainer";
import MenuButton from "../../component/button/MenuButton";
import {openDialogWithPromise} from "../../component/dialog/DialogManager";
import {NewFolderDialogIType} from "./component/dialogs/NewFolderDialog";
import AddLinkIcon from '@mui/icons-material/AddLink';
import {NewLinkFileDialogIType} from "./component/dialogs/NewLinkFileDialog";
import Selecto from "react-selecto";
import {print} from "../../api/util/Logging";
import {FormControlLabel, Typography} from "../../component/wrapper/MuiWrappers";
import {isMobile} from "react-device-detect";
import FileUploadIcon from "@mui/icons-material/FileUpload";
import {DriverUploadDialogIType} from "./component/dialogs/DriverUploadDialog";
import {Condition} from "../../component/container/Condition";
import {useIsUserOrAdmin} from "../../hook/CommonUiHook";
import CopySiteLinkButton from "./component/actions/CopySiteLinkButton";
import {MoveDriverFileDialogIType} from "./component/dialogs/MoveDriverFileDialog";
import {DTARGET, menuItemStyle} from "../../api/util/constants";

type DriverWidgetProps = {} & StackProps;

const DriverWidget = ({
                        ...rest
                      }: DriverWidgetProps) => {

  const [searchParams, setSearchParams] = useSearchParams();
  const [update, setUpdate] = React.useState(false);
  const currentFileId = searchParams.get("id");
  const [currentRoot, setCurrentRoot] = React.useState<DriverFileProfile>(DriverFileProfile.create());
  const [isFolder, setIsFolder] = React.useState(true);
  const [currentFile, setCurrentFile] = React.useState<DriverFileProfile[]>([]);
  const [selectedFile, setSelectedFile] = React.useState<DriverFileProfile[]>([]);
  const [sortDirection, setSortDirection] = React.useState<boolean>(false);
  const [currentSorter, setCurrentSorter] = React.useState<SorterStatus>("name");

  // raw path is the current path to the current folder, ends with "/"
  // or the path to the current file, ends with the file name
  const [rawPath, setRawPath] = React.useState<string>("/");
  // path is the array of path to the current folder / file. It does not include the root and the current folder
  // it is calculated from rawPath
  const [path, setPath] = React.useState<string[]>([]);
  const [owner, setOwner] = React.useState<CustomerProfile | undefined>(void 0);
  const canEdit = useIsUserOrAdmin(owner?.id);

  const {showAlert} = React.useContext(AlertContext);
  const {t} = useTranslation();
  const [currentLayout, setCurrentLayout] = React.useState<LayoutStatus>("stack");
  const [continueSelect, setContinueSelect] = React.useState(false);
  const [selectingMode, setSelectingMode] = React.useState(false);
  const selectoRef = useRef<Selecto>(null);
  const hasSelected = selectedFile.length > 0;

  useEffect(() => {
    // load data
    getFile(currentFileId).then((response) => {
      setCurrentRoot(response);
      setSelectedFile([]);
      selectoRef.current?.setSelectedTargets([]);
      if (response.isFolder) {
        if (response.name !== "") {
          setRawPath(response.path + response.name + "/");
        } else {
          setRawPath("/");
        }
      } else {
        setRawPath(response.path + response.name);
      }
      setOwner(response.owner);
      setIsFolder(response.isFolder);
      if (response.isFolder) {
        setCurrentFile(response.children.sort((a, b) => {
          return fileComparator(a, b, currentSorter, sortDirection);
        }));
      } else {
        setCurrentFile([response]);
      }
    }).catch((error) => {
      showAlert(error.message, "error");
    });
  }, [currentFileId, update]);

  useEffect(() => {
    if (rawPath === "/") {
      setPath([]);
    } else {
      const absPath = rawPath.endsWith("/") ? rawPath.slice(0, rawPath.length - 1) : rawPath;
      if (absPath === "/") {
        setPath([]);
        return
      }
      const finalPath = absPath.slice(1, absPath.lastIndexOf("/"));
      if (finalPath === "") {
        setPath([]);
        return;
      } else {
        setPath(finalPath.split("/"));
      }
    }
  }, [rawPath]);

  useEffect(() => {
    setCurrentFile((existing) => {
      return [...existing].sort((a, b) => {
        return fileComparator(a, b, currentSorter, sortDirection);
      });
    });
  }, [currentSorter, sortDirection]);

  const fileActions: React.ReactElement[] = [];
  if (currentRoot.isFolder && canEdit) {
    fileActions.push(
        <ContentButton
            disabled={!isFolder}
            // a trick to avoid unselecting the selected file
            onMouseEnter={() => {
              setContinueSelect(true);
            }}
            onMouseLeave={() => {
              setContinueSelect(false);
            }
            }
            icon={<CreateNewFolderIcon/>} title={"New Folder"} onClick={(e) => {
          e.stopPropagation();
          print(selectedFile);
          openDialogWithPromise<NewFolderDialogIType, DriverFileProfile>("NewFolderDialog", {
            filePath: rawPath,
            public: currentRoot.isPublic
          })
          .then((newFolderFile) => {
            if (newFolderFile.data) {
              print(newFolderFile.data);
              setCurrentFile(existing => [...existing, newFolderFile.data as DriverFileProfile]);
            }
          }).catch((error) => {
            showAlert(error.message, "error");
          });
        }}/>,
        <ContentButton
            disabled={!isFolder}
            icon={<AddLinkIcon/>} title={"New Link File"} onClick={() => {
          openDialogWithPromise<NewLinkFileDialogIType, DriverFileProfile>("NewLinkFileDialog", {
            filePath: rawPath,
            public: currentRoot.isPublic
          })
          .then((newLinkFile) => {
            if (newLinkFile.data) {
              setCurrentFile(existing => [...existing, (newLinkFile.data as DriverFileProfile)]);
            }
          }).catch((error) => {
            showAlert(error.message, "error");
          });
        }}/>,
        <ContentButton
            disabled={!isFolder}
            icon={<FileUploadIcon/>} title={"Upload"} onClick={() => {
          openDialogWithPromise<DriverUploadDialogIType, void>("DriverUploadDialog", {
            path: rawPath,
            public: currentRoot.isPublic
          })
          .then(() => {
            setUpdate(!update);
          }).catch((error) => {
            showAlert(error.message, "error");
          });
        }}/>
    )
    if (selectedFile.length >= 1) {
      fileActions.push(<ContentButton
          icon={<DriveFileMoveIcon/>} title={"Move"}
          onMouseEnter={() => {
            setContinueSelect(true);
          }}
          onMouseLeave={() => {
            setContinueSelect(false);
          }}
          onClick={() => {
            openDialogWithPromise<MoveDriverFileDialogIType, void>("MoveDriverFileDialog", {
              fileId: selectedFile.map((value) => value.id),
              currentPath: rawPath
            }).then((result) => {
              if (result) {
                setUpdate(!update);
              }
            })
          }}/>);

      fileActions.push(<ContentButton
          color={"error"}
          icon={<DeleteIcon/>} title={"Delete"}
          onMouseEnter={() => {
            setContinueSelect(true);
          }}
          onMouseLeave={() => {
            setContinueSelect(false);
          }}
          onClick={() => {
            deleteFile(selectedFile.map((value) => value.id)).then
            (() => {
              setUpdate(!update);
            }).catch((error) => {
              showAlert(error.message, "error");
            });
          }}/>);


    }
  }

  const openAction = (item: DriverFileProfile) => {
    if (item.isFolder) {
      setSearchParams({id: item.id});
    } else {
      window.open(getLink(item), DTARGET);
    }
  }

  const selectAction = (e: React.MouseEvent<HTMLDivElement, MouseEvent>, fileItem: DriverFileProfile) => {
    e.stopPropagation();
    let newSelectedFileItems: DriverFileProfile[];
    if (!selectingMode) {

      if (e.ctrlKey) {
        if (selectedFile.includes(fileItem)) {
          newSelectedFileItems = selectedFile.filter((value) => value !== fileItem);
        } else {
          newSelectedFileItems = [...selectedFile, fileItem];
        }
      } else if (e.shiftKey) {
        if (selectedFile.length === 0) {
          newSelectedFileItems = [fileItem];
        } else {
          const newFileToSelect = [];
          let founded = false;
          for (let i = 0; i < currentFile.length; i++) {
            const fileKey = currentFile[i];
            if (fileKey === selectedFile[selectedFile.length - 1] || fileKey === fileItem) {
              founded = !founded;
              if (!founded) {
                if (!selectedFile.includes(fileKey)) {
                  newFileToSelect.push(fileKey);
                }
                break;
              }
            }
            if (founded) {
              if (!selectedFile.includes(fileKey)) {
                newFileToSelect.push(fileKey);
              }
            }
          }
          newSelectedFileItems = [...selectedFile, ...newFileToSelect];
        }
      } else {
        if (selectedFile.includes(fileItem)) {
          const selected = selectedFile.filter((value) => value !== fileItem);
          newSelectedFileItems = selected;
        } else {
          newSelectedFileItems = [fileItem];
        }
      }


    } else {
      // 加选
      if (e.shiftKey) {
        if (selectedFile.length === 0) {
          newSelectedFileItems = [fileItem];
        } else {
          const newFileToSelect = [];
          let founded = false;
          for (let i = 0; i < currentFile.length; i++) {
            const fileKey = currentFile[i];
            if (fileKey === selectedFile[selectedFile.length - 1] || fileKey === fileItem) {
              founded = !founded;
              if (!founded) {
                if (!selectedFile.includes(fileKey)) {
                  newFileToSelect.push(fileKey);
                }
                break;
              }
            }
            if (founded) {
              if (!selectedFile.includes(fileKey)) {
                newFileToSelect.push(fileKey);
              }
            }
          }
          newSelectedFileItems = [...selectedFile, ...newFileToSelect];
        }
      } else {
        if (selectedFile.includes(fileItem)) {
          newSelectedFileItems = selectedFile.filter((value) => value !== fileItem);
        } else {
          newSelectedFileItems = [...selectedFile, fileItem];
        }
      }
    }
    selectoRef.current?.setSelectedTargets(newSelectedFileItems.map((value) => document.getElementById(value.id) as HTMLElement));
    setSelectedFile(newSelectedFileItems);
  }

  const menuItems = (item: DriverFileProfile) => {
    const allItems: React.ReactElement[] = [];
    if (item.isFolder) {
      allItems.push(<OpenFolderButton folderId={item.id} target={DTARGET} sx={menuItemStyle}/>);
    }
    // else {
    //   // copy link
    //   allItems.push(<CopySiteLinkButton itemId={item.id} />);
    //
    //
    //   // allItems.push(<a href={getLink(item)}
    //   //                  target={isMobile ? "_self" : "_blank"}
    //   //                  rel={"noreferrer"}
    //   //                  style={{textDecoration: "none", color: "inherit", margin: "0px",padding: "0px"}}>
    //   //   <FileSourceButton url={getLink(item)}  target={isMobile ? "_self" : "_blank"}/>
    //   // </a>);
    // }
    allItems.push(<CopySiteLinkButton itemId={item.id} sx={menuItemStyle}/>);
    if (canEdit) {
      allItems.push(<ShareButton fileId={item.id} isPublic={item.isPublic}
                                 sx={menuItemStyle}
                                 onVisibilityChange={() => {
                                   showAlert("Success", "success");
                                   setUpdate(!update)
                                 }}/>);
      allItems.push(<FileRenameButton fileId={item.id} existingName={item.name}
                                      sx={menuItemStyle}
                                      onRename={() => {
                                        showAlert("Success", "success");
                                        setUpdate(!update)
                                      }}/>);

      allItems.push(<ContentButton
          sx={menuItemStyle}
          icon={<DriveFileMoveIcon/>} title={"Move"}
          onClick={() => {
            openDialogWithPromise<MoveDriverFileDialogIType, void>("MoveDriverFileDialog", {
              fileId: [item.id],
              currentPath: item.path
            }).then((result) => {
              if (result) {
                setUpdate(!update);
              }
            })
          }}/>);

      allItems.push(<DeleteButton fileId={item.id}
                                  sx={menuItemStyle}
                                  onDelete={() => {
                                    showAlert("Success", "success");
                                    setUpdate(!update)
                                  }}/>);


    }
    return allItems;
  }

  const moveFileToFolder = (item: DriverFileProfile) => {
    if (item.isFolder) {
      const fileIdToMove = selectedFile.filter((value) => {
        return value !== item;
      }).map((value) => value.id);
      moveFile(fileIdToMove, item.path + item.name + "/", item.isPublic).then(() => {
        if (item.isPublic) {
          showAlert("Items are moved to a public folder", "success");
        } else {
          showAlert("Items are moved to a private folder", "success");
        }
        setUpdate(!update);
      }).catch((error) => {
        showAlert(error.message, "error");
      });

    }
  }

  return <Stack direction={"column"} minHeight={"78dvh"}
                onClick={() => {
                  if (selectingMode || hasSelected) {
                    setSelectedFile([]);
                    selectoRef.current?.setSelectedTargets([]);
                  }
                }}
                {...rest}>
    <Breadcrumbs sx={{paddingX: 1}}>
      <Link onClick={
        () => {
          setSearchParams({});
        }
      }
            onDragOver={(e) => {
              e.preventDefault();
            }}
            onDrop={(e) => {
              const fileIdToMove = selectedFile.map((value) => value.id);
              moveFile(fileIdToMove, "/").then(() => {
                showAlert("Items are moved to the root folder", "success");
                setUpdate(!update);
              }).catch((error) => {
                showAlert(error.message, "error");
              });
            }}

      >Home:</Link>
      {
          path.length > 0 && path.map((item, index) => {
            return <Link
                key={index}
                onClick={
                  () => {
                    const newPath = "/" + path.slice(0, index).join("/") + item;
                    getFileId(newPath, owner?.id || "").then((response) => {
                      setSearchParams({id: response});
                    }).catch((error) => {
                      showAlert(error.message, "error");
                    });
                  }
                }
                onDragOver={(e) => {
                  e.preventDefault();
                }}
                onDrop={(e) => {
                  const fileIdToMove = selectedFile.map((value) => value.id);
                  const newPath = "/" + path.slice(0, index).join("/") + item + "/";
                  moveFile(fileIdToMove, newPath).then(() => {
                    showAlert("Items are moved to " + newPath, "success");
                    setUpdate(!update);
                  }).catch((error) => {
                    showAlert(error.message, "error");
                  });
                }}
            >{item}</Link>
          })
      }
      {currentRoot.name && currentRoot.name !== "" && <Link>{currentRoot.name}</Link>}
    </Breadcrumbs>
    <Stack direction={"row"} justifyContent={"start"} marginY={2} spacing={1} alignItems={"center"}>
      <BreakpointContainer up={"md"}>
        <ListComponent data={fileActions} renderItem={(item, index) => {
          return <ContentButton key={index}
                                variant={"outlined"}
                                color={"primary"}
                                disableElevation={true}  {...item.props} />
        }}/>
      </BreakpointContainer>
      <BreakpointContainer down={"md"}>
        <Condition show={fileActions.length > 0}>
          <MenuButton icon=<MenuIcon/> menuAlign={"left"} menuItems={fileActions}
                      stopPropagation={true}/>
        </Condition>
      </BreakpointContainer>
      <LayoutSwitchButton currentLayout={currentLayout}
                          size={isMobile ? "small" : "large"}
                          variant={"outlined"} color={"primary"}
                          onLayoutChange={setCurrentLayout}
                          stopPropagation={true}
      />
      <SorterButton currentSorter={currentSorter}
                    stopPropagation={true}
                    size={isMobile ? "small" : "large"}
                    variant={"outlined"} color={"primary"}
                    icon={<SortIcon/>}
                    onSorterChange={(newSorter) => {
                      setCurrentSorter(newSorter);
                      setSortDirection(!sortDirection);
                    }}/>
      {isMobile && <FormControlLabel
          value="selecting"
          sx={{
            paddingX: -3,
            marginX: -3
          }}
          control={<Switch color="primary" checked={selectingMode}
                           onClick={(e) => {
                             e.stopPropagation();
                           }}
                           sx={{
                             marginX: "-10px"
                           }}
                           onChange={(e) => {
                             setSelectingMode(!selectingMode)
                           }}/>}
          label={<Typography
              style={{marginBottom: "-10px", fontSize: "small"}}>{"Select"}</Typography>}
          labelPlacement="top"
      />}


      {isMobile && selectingMode && <FormControlLabel
          sx={{
            paddingX: -3,
            marginX: -3
          }}
          value="selectAll"
          control={<Checkbox color="primary" checked={selectedFile.length === currentFile.length}
                             onClick={(e) => {
                               e.stopPropagation();
                             }}
                             sx={{
                               marginX: "-10px"
                             }}
                             onChange={(e) => {
                               if (selectedFile.length === currentFile.length) {
                                 setSelectedFile([]);
                                 selectoRef.current?.setSelectedTargets([]);
                                 return;
                               } else {
                                 setSelectedFile([...currentFile]);
                                 selectoRef.current?.setSelectedTargets(currentFile.map((value) => document.getElementById(value.id) as HTMLElement));
                               }
                             }}/>}
          label={<Typography
              style={{marginBottom: "-10px", fontSize: "small"}}>{"All"}</Typography>}
          labelPlacement="top"
      />}

    </Stack>

    <Selecto
        ref={selectoRef}

        // The container to add a selection element
        container={document.body}
        // The area to drag selection element (default: container)
        dragContainer={window}
        // Targets to select. You can register a queryselector or an Element.
        selectableTargets={[".target"]}
        // Whether to select by click (default: true)
        selectByClick={false}
        // Whether to select from the target inside (default: true)
        selectFromInside={true}
        preventClickEventOnDrag={true}
        // After the select, whether to select the next target with the selected target (deselected if the target is selected again).
        continueSelect={continueSelect}
        // Determines which key to continue selecting the next target via keydown and keyup.
        toggleContinueSelect={[["shift"], ["ctrl"]]}
        // The container for keydown and keyup events
        keyContainer={window.document.querySelector(".select-zone") as HTMLElement}

        dragCondition={e => {
          return !hasSelected;
        }}

        // The rate at which the target overlaps the drag area to be selected. (default: 100)
        hitRate={1}

        onSelect={e => {
          if (selectingMode) {
            return;
          }

          const newSelectedFileItems: DriverFileProfile[] = [];
          for (const target of e.selected) {
            const id = target.id;
            const fileItem = currentFile.find((value) => value.id === id);
            if (fileItem) {
              newSelectedFileItems.push(fileItem);
            }
          }
          setSelectedFile(newSelectedFileItems);
        }}
    />
    <OneOf index={currentLayout === "stack" ? 0 : 1}>
      <Stack direction={"column"}>
        <ListComponent data={currentFile} renderItem={(item, index) => {
          return <Stack direction={"column"} key={item.id}>
            <FileItemRow fileItem={item}
                         id={item.id}
                         selected={selectedFile.includes(item)}
                         className={"target"}
                         openAction={openAction}
                         onClick={(e) => {
                           selectAction(e, item);
                         }}
                         onDoubleClick={() => {
                           openAction(item);
                         }
                         }
                         menuItems={menuItems(item)}
                         draggable={hasSelected}
                         onDragStart={(e: React.DragEvent) => {
                         }}
                         onDragOver={item.isFolder ? (e: React.DragEvent) => {
                           e.preventDefault();
                         } : void 0}
                         onDrop={(e: React.DragEvent) => {
                           e.stopPropagation()
                           moveFileToFolder(item);
                         }}
            />
            <Divider/></Stack>
        }}/>
      </Stack>
      <Grid container alignItems={"center"}
            width={"fit-content"}
            justifyContent={"start"}>
        <ListComponent data={currentFile} renderItem={(item, index) => {
          return <FileItemBlock key={item.id} fileItem={item}
                                id={item.id}
                                className={"target"}
                                selected={selectedFile.includes(item)}
                                onClick={(e) => {
                                  e.stopPropagation();
                                  selectAction(e, item);
                                }}
                                onDoubleClick={() => {
                                  openAction(item);
                                }}
                                openAction={openAction}
                                menuItems={menuItems(item)}
                                draggable={hasSelected}
                                onDragStart={(e: React.DragEvent) => {
                                }}
                                onDragOver={item.isFolder ? (e: React.DragEvent) => {
                                  e.preventDefault();
                                } : void 0}
                                onDrop={(e: React.DragEvent) => {
                                  print("onDrop", e);
                                  e.stopPropagation()
                                  moveFileToFolder(item);
                                }}
          />
        }}/>
      </Grid>
    </OneOf>
    <Stack direction={"column"} justifyContent={"center"} alignItems={"flex-start"} marginTop={2}>
      <OneOf index={selectedFile.length > 0 ? 0 : 1}>
        <Typography variant={"body1"} color={"textSecondary"} textAlign={"center"}
                    enableTranslate={false}>{
            selectedFile.length + " " + t("selected") + ", " + getReadableFileSize(getTotalSize(selectedFile))
        }</Typography>
        <Typography variant={"body1"} color={"textSecondary"} textAlign={"center"}
                    enableTranslate={false}>{
            currentFile.length + " " + t("items") + ", " + getReadableFileSize(getTotalSize(currentFile))
        }</Typography>
      </OneOf>
    </Stack>

  </Stack>;
};

export default React.memo(DriverWidget);