import * as React from 'react';
import ContentButton, {ContentButtonProps} from "./ContentButton";
import MenuButton, {MenuButtonProps} from "./MenuButton";
import {menuItemStyle} from "../../api/util/constants";

// 可显示当前状态的菜单按钮，点击后执行按钮的函数并更新状态
export type MultiStatusButtonProps = {
  currentStatus: string,
  statusToContentButtonProps: {
    [key: string]: ContentButtonProps,
  }
  onStatusChange: (newStatus: string) => void,
} & Omit<MenuButtonProps, "menuItems">;
export const MultiStatusButton = ({
                                    currentStatus,
                                    statusToContentButtonProps,
                                    onStatusChange,
                                    ...rest
                                  }: MultiStatusButtonProps) => {
  return <MenuButton
      title={statusToContentButtonProps[currentStatus].title}
      menuItems={Object.keys(statusToContentButtonProps).map((status) => {
        const {onClick, ...rest2} = statusToContentButtonProps[status];
        return <ContentButton key={status}
                              sx = {menuItemStyle}
                              onClick={(e) => {
                                if (onClick) {
                                  onClick(e);
                                }
                                onStatusChange(status);
                              }}
                              {...rest2}/>;
      })}
      {...rest} />;
};

