import React, {useCallback} from "react";
import {Menu, MenuItem, Stack, StackProps} from "@mui/material";
import ListComponent from "../container/ListComponent";
import {useLongPress} from 'use-long-press';
import {isMobile} from 'react-device-detect';

type ContextMenuProps = {
  menuItems: React.ReactElement[],
  enableLongPress?: boolean,
} & StackProps;
const ContextMenuWithLongPres = ({
                                   menuItems,
                                   children,
                                   ...rest
                                 }: Omit<ContextMenuProps, "enableLongPress">) => {

  const [contextMenu, setContextMenu] = React.useState<{
    mouseX: number;
    mouseY: number;
  } | null>(null);

  const handleContextMenu = useCallback((event: any) => {
    event.preventDefault();
    event.stopPropagation();
    setContextMenu(
        contextMenu === null
            ? {
              mouseX: event.clientX + 2,
              mouseY: event.clientY - 6,
            }
            : // repeated contextmenu when it is already open closes it with Chrome 84 on Ubuntu
              // Other native context menus might behave different.
              // With this behavior we prevent contextmenu from the backdrop to re-locale existing context menus.
            null,
    );
  }, []);

  const handleContextMenuLongPress = useCallback((event: any) => {
    event.preventDefault();
    event.stopPropagation();

    setContextMenu(
        contextMenu === null
            ? {
              mouseX: event.clientX > window.screen.width / 2 ? event.clientX - 200 : event.clientX + 100,
              mouseY: event.clientY - 6,
            }
            : // repeated contextmenu when it is already open closes it with Chrome 84 on Ubuntu
              // Other native context menus might behave different.
              // With this behavior we prevent contextmenu from the backdrop to re-locale existing context menus.
            null,
    );
  }, []);

  const longPressEvent = useLongPress(handleContextMenuLongPress, {
    threshold: 500,
  });


  const handleClose = () => {
    setContextMenu(null);
  };
  if (menuItems.length === 0) {
    return <Stack {...rest}>
      {children}
    </Stack>;
  }


  return <Stack onContextMenu={handleContextMenu}  {...longPressEvent()} {...rest}>
    {children}
    <Menu
        open={contextMenu !== null}
        onClose={handleClose}
        anchorReference="anchorPosition"
        anchorPosition={
          contextMenu !== null
              ? {top: contextMenu.mouseY, left: contextMenu.mouseX}
              : undefined
        }
    >
      <ListComponent data={menuItems} renderItem={(item: React.ReactElement, index?: number) => {
        // special handling for anchor tags
        if (item.type === "a") {
          return <a href={item.props.href}
                    style={item.props.style}
                    target={item.props.target}
                    download={item.props.download}
                    rel={item.props.rel}
                    onClick={(e) => {
                      handleClose();
                      e.stopPropagation();
                    }}
                    onContextMenu={(e) => {
                      // use browser context menu
                      e.stopPropagation();
                    }}><MenuItem key={index}
                                 sx={{padding: 0, margin: 0}}
          >{item.props.children}</MenuItem></a>;
        }
        return <MenuItem key={index} onClick={(e) => {
          handleClose();
        }}
        sx={{padding: 0, margin: 0}}
        >{item}</MenuItem>;
      }}/>
    </Menu>
  </Stack>;
}


const ContextMenuWithoutLongPress = ({
                                       menuItems,
                                       children,
                                       ...rest
                                     }: Omit<ContextMenuProps, "enableLongPress">) => {

  const [contextMenu, setContextMenu] = React.useState<{
    mouseX: number;
    mouseY: number;
  } | null>(null);

  const handleContextMenu = useCallback((event: any) => {
    event.preventDefault();
    event.stopPropagation();
    setContextMenu(
        contextMenu === null
            ? {
              mouseX: event.clientX + 2,
              mouseY: event.clientY - 6,
            }
            : // repeated contextmenu when it is already open closes it with Chrome 84 on Ubuntu
              // Other native context menus might behave different.
              // With this behavior we prevent contextmenu from the backdrop to re-locale existing context menus.
            null,
    );
  }, []);

  const handleClose = () => {
    setContextMenu(null);
  };
  if (menuItems.length === 0) {
    return <Stack {...rest}>
      {children}
    </Stack>;
  }
  return <Stack onContextMenu={handleContextMenu} {...rest}>
    {children}
    <Menu
        open={contextMenu !== null}
        onClose={handleClose}
        anchorReference="anchorPosition"
        anchorPosition={
          contextMenu !== null
              ? {top: contextMenu.mouseY, left: contextMenu.mouseX}
              : undefined
        }
    >
      <ListComponent data={menuItems} renderItem={(item: React.ReactElement, index?: number) => {
        // special handling for anchor tags
        if (item.type === "a") {
          return <a href={item.props.href}
                    style={item.props.style}
                    target={item.props.target}
                    download={item.props.download}
                    rel={item.props.rel}
                    onClick={(e) => {
                      handleClose();
                      e.stopPropagation();
                    }}
                    onContextMenu={(e) => {
                      // use browser context menu
                      e.stopPropagation();
                    }}><MenuItem key={index}
          >{item.props.children}</MenuItem></a>;
        }
        return <MenuItem key={index}
                         sx={{padding: 0, margin: 0}}
                         onClick={(e) => {
          handleClose();
        }}>{item}</MenuItem>;
      }}/>
    </Menu>
  </Stack>;
}

const ContextMenu = ({
                       menuItems,
                       enableLongPress = isMobile,
                       children,
                       ...rest
                     }: ContextMenuProps) => {
  if (enableLongPress) {
    return <ContextMenuWithLongPres menuItems={menuItems} children={children} {...rest}/>;
  } else {
    return <ContextMenuWithoutLongPress menuItems={menuItems} children={children} {...rest}/>;
  }
}

export default ContextMenu;