import {SvgIconProps} from '@mui/material/SvgIcon';
import * as React from 'react';
import FolderIcon from "@mui/icons-material/Folder";
import LanguageIcon from '@mui/icons-material/Language';
import InsertDriveFileIcon from "@mui/icons-material/InsertDriveFile";
import InsertPhotoIcon from '@mui/icons-material/InsertPhoto';
import DescriptionIcon from '@mui/icons-material/Description';
import VideoFileIcon from '@mui/icons-material/VideoFile';
import AudioFileIcon from '@mui/icons-material/AudioFile';
import FontDownloadIcon from '@mui/icons-material/FontDownload';
import CssIcon from '@mui/icons-material/Css';
import JavascriptIcon from '@mui/icons-material/Javascript';
import HtmlIcon from '@mui/icons-material/Html';


type FileIconProp = {
  isFolder: boolean;
  contentType?: string;
  isPublic: boolean;
} & SvgIconProps;

export const FileIcon = ({
                           contentType = void 0,
                           isFolder = false,
                           isPublic = false,
                           ...rest
                         }: FileIconProp) => {
  if (isFolder) {
    return <FolderIcon {...rest}/>
  }
  if (!contentType) {
    return <LanguageIcon {...rest}/>;
  }
  const type = contentType.includes("/") ? contentType.split("/")[0] : contentType;
  const subType = contentType.includes("/") ? contentType.split("/")[1] : "";
  switch (type) {
    case "image":
      return <InsertPhotoIcon {...rest}/>
    case "text":
      switch (subType) {
        case "css":
          return <CssIcon {...rest}/>
        case "javascript":
          return <JavascriptIcon {...rest}/>
        case "html":
          return <HtmlIcon {...rest}/>
        default:
          return <DescriptionIcon {...rest}/>
      }
    case "video":
      return <VideoFileIcon {...rest}/>
    case "audio":
      return <AudioFileIcon {...rest}/>
    case "font":
      return <FontDownloadIcon {...rest}/>
    default:
      return <InsertDriveFileIcon {...rest}/>
  }
};
