import * as React from 'react';
import {DriverFileProfile} from "../../../proto/application/driver/DriverMessage";
import ContextMenu from "../../../component/menu/ContextMenu";
import {Badge, Box, StackProps} from "@mui/material";
import {FileIcon} from "./FileIcon";
import {Typography} from "../../../component/wrapper/MuiWrappers";
import {getReadableFileSize, toVisibility} from "../util/Utils";
import LockIcon from '@mui/icons-material/Lock';
import DoneIcon from '@mui/icons-material/Done';
import MenuButton from "../../../component/button/MenuButton";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import {isMobile} from 'react-device-detect';
import {FileItemLink} from "./FileItemLink";
import {timeForRender} from "../../../api/util/TimeUtil";

type FileItemBlockProps = {
  fileItem: DriverFileProfile;
  selected: boolean;
  menuItems?: React.ReactElement[];
  iconSize?: number;
  openAction?: (fileItem: DriverFileProfile) => void;
} & StackProps;
export const FileItemBlock = ({
                                fileItem,
                                selected,
                                menuItems,
                                iconSize = 6,
                                openAction,
                                ...rest
                              }: FileItemBlockProps) => {
  const isFolder = fileItem.isFolder;
  const isPublic = fileItem.isPublic;
  return <ContextMenu direction={"column"} spacing={1} alignItems={"center"} textAlign={"center"}
                      flexShrink={1}
                      enableLongPress={isMobile}
                      menuItems={menuItems ?? []}
                      sx={{
                        backgroundColor: selected ? "rgba(194, 231, 255,1)" : "transparent",
                        paddingY: 1,
                        paddingX: 1.5,
                        borderRadius: 1,
                        '&:hover': {
                          backgroundColor: selected ? "rgba(194, 231, 255,1)" : "rgba(0, 0, 0, 0.1)",
                        }
                      }}
                      title={`${timeForRender(fileItem.lastModifiedDate)}\n${getReadableFileSize(fileItem.fileMetaData?.sizeInBytes)}\n${toVisibility(fileItem.isPublic)}`}
                      {...rest}>
    <Box>
      <Badge sx={{
        "& .MuiBadge-badge": {height: iconSize * 4, minWidth: iconSize * 3}
      }}
             badgeContent={
               (isMobile && menuItems && menuItems.length > 0) ?
                   <MenuButton icon={<MoreVertIcon color={"success"}/>} menuAlign={"right"}
                               stopPropagation={true}
                               menuItems={menuItems}/> : void 0
             }

             overlap="circular"
             anchorOrigin={{
               vertical: 'top',
               horizontal: 'right',
             }}>
        <Badge sx={{
          "& .MuiBadge-badge": {height: iconSize * 4, minWidth: iconSize * 3}
        }}
               badgeContent={isPublic ? <DoneIcon sx={{
                 fontSize: `${iconSize / 5}rem`
               }}/> : <LockIcon sx={{
                 fontSize: `${iconSize / 5}rem`
               }}/>}
               color={isPublic ? "success" : "warning"}
               overlap="circular"

               anchorOrigin={{
                 vertical: 'bottom',
                 horizontal: 'right',
               }}>
          <FileIcon isFolder={isFolder} contentType={fileItem.fileMetaData?.contentType}
                    isPublic={isPublic} color={selected ? "primary" : "inherit"} sx={{
            fontSize: `${iconSize}rem`
          }}/>
        </Badge>
      </Badge>
      <Typography variant={"body1"} enableTranslate={false} flexGrow={1}>
        <FileItemLink fileItem={fileItem} openAction={openAction}/></Typography>
      <Typography variant={"caption"}
                  enableTranslate={false}>{getReadableFileSize(fileItem.fileMetaData?.sizeInBytes)}</Typography>
    </Box>
  </ContextMenu>;
}