import * as React from 'react';
import ContentButton, {ContentButtonProps} from "../../../../component/button/ContentButton";
import {openDialogWithPromise} from "../../../../component/dialog/DialogManager";
import DriveFileRenameOutlineIcon from '@mui/icons-material/DriveFileRenameOutline';

type FileRenameButtonProps = {
  fileId: string,
  existingName: string,
  onRename: () => void,

} & ContentButtonProps;
export const FileRenameButton = ({
                                   fileId,
                                   existingName,
                                   onRename,
                                   title = "Rename",
                                   ...rest
                                 }: FileRenameButtonProps) => {
  return <ContentButton
      icon={<DriveFileRenameOutlineIcon/>}
      onClick={async () => {
        const result = await openDialogWithPromise("RenameDriverFileDialog", {
          filename: existingName,
          fileId: fileId
        });
        if (result.success) {
          onRename();
        }
      }}
      title={title}
      {...rest}
  ></ContentButton>;
};