// @flow
import * as React from 'react';
import {MultiStatusButton} from "../../../../component/button/MultiStatusButton";
import {MenuButtonProps} from "../../../../component/button/MenuButton";
import GridViewIcon from '@mui/icons-material/GridView';
import ViewListIcon from '@mui/icons-material/ViewList';

export type LayoutStatus = "stack" | "grid";

const statusToContentButtonProps = {
  stack: {
    title: "List",
    icon: <ViewListIcon/>
  },
  grid: {
    title: "Grid",
    icon: <GridViewIcon/>
  },
}

export type LayoutSwitchButtonProps = {
  currentLayout: LayoutStatus;
  onLayoutChange: (layout: LayoutStatus) => void;
} & Omit<MenuButtonProps, "menuItems">;

export const LayoutSwitchButton = ({
                                     currentLayout,
                                     onLayoutChange,
                                     ...rest
                                   }: LayoutSwitchButtonProps) => {
  return <MultiStatusButton
      currentStatus={currentLayout}
      statusToContentButtonProps={statusToContentButtonProps}
      onStatusChange={(status) => {
        onLayoutChange(status as LayoutStatus);
      }}
      icon={statusToContentButtonProps[currentLayout].icon}
      {...rest}
  ></MultiStatusButton>
};