// @flow
import * as React from 'react';
import {DriverFileProfile} from "../../../proto/application/driver/DriverMessage";
import ContextMenu from "../../../component/menu/ContextMenu";
import {Stack, StackProps} from "@mui/material";
import BreakpointContainer from "../../../component/container/BreakpointContainer";
import {FileIcon} from "./FileIcon";
import {Typography} from "../../../component/wrapper/MuiWrappers";
import {getFileType, getReadableFileSize, toVisibility} from "../util/Utils";
import MoreVertIcon from '@mui/icons-material/MoreVert';
import MenuButton from "../../../component/button/MenuButton";
import {isMobile} from 'react-device-detect';
import {FileItemLink} from "./FileItemLink";
import {timeForRender} from "../../../api/util/TimeUtil";

type FileItemRowProps = {
  fileItem: DriverFileProfile;
  selected: boolean;
  menuItems?: React.ReactElement[];
  openAction?: (fileItem: DriverFileProfile) => void;
} & StackProps;
export const FileItemRow = ({
                              fileItem,
                              selected,
                              menuItems,
                              openAction,
                              ...rest
                            }: FileItemRowProps) => {
  const isFolder = fileItem.isFolder;
  const isPublic = fileItem.isPublic;
  return <ContextMenu direction={"row"} spacing={1} alignItems={"center"} textAlign={"start"}
                      menuItems={menuItems ?? []}
                      enableLongPress={isMobile}
                      sx={{
                        backgroundColor: selected ? "rgba(194, 231, 255,1)" : "transparent",
                        paddingY: 1.5,
                        paddingX: 2,
                        '&:hover': {
                          backgroundColor: selected ? "rgba(194, 231, 255,1)" : "rgba(0, 0, 0, 0.1)",
                        }
                      }}
                      {...rest}>
    <BreakpointContainer up={"md"}>
      <Stack direction={"row"} spacing={1} alignItems={"center"} className={"pointer"} flexGrow={1}>
        <FileIcon isFolder={isFolder} contentType={fileItem.fileMetaData?.contentType}
                  isPublic={isPublic} color={selected ? "primary" : "inherit"}/>
        <Typography variant={"body1"} enableTranslate={false}
                    flexGrow={1}>
          <FileItemLink fileItem={fileItem} openAction={openAction}/>
        </Typography>
        <Typography variant={"body1"} enableTranslate={false}
                    flexBasis={"100px"}>{getFileType(fileItem)}</Typography>
        <Typography variant={"body1"}
                    enableTranslate={false}
                    flexBasis={"200px"}>{timeForRender(fileItem.lastModifiedDate)}</Typography>
        <Typography variant={"body1"}
                    enableTranslate={false}
                    flexBasis={"100px"}>{getReadableFileSize(fileItem.fileMetaData?.sizeInBytes)}</Typography>
        <Typography variant={"body1"}
                    enableTranslate={false}
                    flexBasis={"100px"}>{toVisibility(fileItem.isPublic)}</Typography>
      </Stack>
    </BreakpointContainer>
    <BreakpointContainer down={"md"}>
      <Stack direction={"row"} spacing={2} alignItems={"center"} className={"pointer"} flexGrow={1}>
        <FileIcon isFolder={isFolder} contentType={fileItem.fileMetaData?.contentType}
                  isPublic={isPublic} fontSize={"large"} color={selected ? "primary" : "inherit"}/>
        <Stack direction={"column"} spacing={1} flexGrow={1}>
          <Typography variant={"body1"} enableTranslate={false}
                      flexGrow={1}> <FileItemLink fileItem={fileItem}
                                                  openAction={openAction}/></Typography>
          <Stack direction={"row"} spacing={1} alignItems={"center"}>
            <Typography variant={"caption"}
                        enableTranslate={false}>{timeForRender(fileItem.lastModifiedDate)}</Typography>
            <Typography variant={"caption"}
                        enableTranslate={false}>{getReadableFileSize(fileItem.fileMetaData?.sizeInBytes)}</Typography>
            <Typography variant={"caption"}
                        enableTranslate={false}>{toVisibility(fileItem.isPublic)}</Typography>
          </Stack>
        </Stack>
      </Stack>
    </BreakpointContainer>
    {(menuItems && menuItems.length > 0 && isMobile) &&
        <MenuButton icon=<MoreVertIcon/> sx={{borderRadius: "500px"}} menuAlign={"right"}
                    stopPropagation={true}
                    menuItems={menuItems}/>}

  </ContextMenu>;
}