import * as React from 'react';
import ContentButton, {ContentButtonProps} from "../../../../component/button/ContentButton";
import {setVisibility} from "../../service/DriverService";
import ShareIcon from '@mui/icons-material/Share';
import LockIcon from '@mui/icons-material/Lock';
import AlertContext from "../../../../context/AlertContext";

type ShareButtonProps = {
  fileId: string,
  isPublic: boolean,
  onVisibilityChange: () => void,
} & ContentButtonProps;
export const ShareButton = ({
                              fileId,
                              isPublic,
                              onVisibilityChange,
                              ...rest
                            }: ShareButtonProps) => {
  const {showAlert} = React.useContext(AlertContext);
  return <ContentButton
      icon={isPublic ? <LockIcon/> : <ShareIcon/>}
      onClick={() => {
        setVisibility(fileId, !isPublic)
        .then(() => {
          onVisibilityChange();
        })
        .catch((e) => {
              showAlert("Failed to change: " + e.message);
            }
        )
      }}
      title={isPublic ? "UnShare" : "Share"} {...rest}
  />;
};