import * as React from 'react';
import ContentButton, {ContentButtonProps} from "../../../../component/button/ContentButton";
import {get_driver_url} from "../../../../navigation/routerUtil";
import OpenInNewIcon from '@mui/icons-material/OpenInNew';

type OpenFolderButtonProps = {
  folderId: string,
  target?: string,
} & ContentButtonProps;
export const OpenFolderButton = ({
                                   folderId,
                                   title = "Open",
                                   target = "_blank",
                                   ...rest
                                 }: OpenFolderButtonProps) => {
  return <ContentButton
      icon={<OpenInNewIcon/>}
      onClick={() => {
    window.open(get_driver_url(folderId), target);
  }}
                        title={title} {...rest}
  />;
};