// @flow
import * as React from 'react';
import ContentButton, {ContentButtonProps} from "../../../../component/button/ContentButton";
import AlertContext from "../../../../context/AlertContext";
import DeleteIcon from '@mui/icons-material/Delete';
import {deleteFile} from "../../service/DriverService";

type DeleteButtonProps = {
  fileId: string;
  onDelete: () => void;
} & ContentButtonProps;
export const DeleteButton = ({
                               fileId,
                               onDelete,
                               title = "Delete",
                               color = "error",
                               ...rest
                             }: DeleteButtonProps) => {
  const {showAlert} = React.useContext(AlertContext);
  return <ContentButton
      icon={<DeleteIcon color={color}/>}
      color={color}
      onClick={() => {
        deleteFile(fileId)
        .then(() => {
          onDelete();
        })
        .catch((e) => {
              showAlert("Failed to delete: " + e.message);
            }
        )
      }}
      title={title} {...rest}
  />;
};