import * as React from 'react';
import ContentButton, {ContentButtonProps} from "../../../../component/button/ContentButton";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import copy from 'copy-to-clipboard';
import {get_driver_url} from "../../../../navigation/routerUtil";

type CopySiteLinkButtonProps = {
  itemId: string,
} & ContentButtonProps;
const CopySiteLinkButton = ({
                                     itemId,
                                     title = "Copy Link",
                                     ...rest
                                   }: CopySiteLinkButtonProps) => {
  return <ContentButton
      icon={<ContentCopyIcon/>}
      onClick={() => {
        copy(window.location.host + get_driver_url(itemId));
      }}
      title={title}
      {...rest}
  ></ContentButton>;
};

export default CopySiteLinkButton;