// @flow
import * as React from 'react';
import {Link} from "@mui/material";
import {DriverFileProfile} from "../../../proto/application/driver/DriverMessage";
import {DTARGET} from "../../../api/util/constants";

type FileItemLinkProps = {
  fileItem: DriverFileProfile;
  openAction?: (fileItem: DriverFileProfile) => void;
};
export const FileItemLink = ({fileItem, openAction}: FileItemLinkProps) => {
  return <Link
      target={fileItem.isFolder ? void 0 : DTARGET}
      onContextMenu={(e) => {
        if (!fileItem.isFolder) {
          e.stopPropagation();
        }
      }}
      href={fileItem.url || fileItem.fileMetaData?.url || void 0}
      component={"a"}
      onClick={(e) => {
        if (fileItem.isFolder) {
          e.stopPropagation();
          openAction?.(fileItem);
        }
      }}
  >{fileItem.name}</Link>;
};